import { Component, OnInit } from '@angular/core';
import { CommonUtils } from '../../psServices/models/common-utils';
import { IOptionsPsActionImageExposed } from '../../psServices/models/ps-common-interface';

@Component({
  templateUrl: './ps-dummy-screen.page.html',
  selector: 'ps-dummy-screen'
})
export class PsDummyScreenPage implements OnInit {
  public loaderOptions: IOptionsPsActionImageExposed;

  constructor() { }

  ngOnInit() {
    this.loaderOptions = {
      imageName: unescape(CommonUtils.getCssVariableValue('--ps-loader-image-name')),
      allowCust: false
    };
  }
}
